import * as React from 'react'
import * as ReactDOM from 'react-dom'
import { useState, /*useEffect,*/ useContext, createContext} from 'react'
import axios from 'axios'
/* 10項目の項目（スキーマ）を用意し、画面で入力したデータを登録、編集、削除するアプリを作成する。
項目名は和名と英名を用意し、和名を画面描画に使用し、英名をスキーマ名に使用すること
例）和名：名前　英名：name
一覧画面（index.html）を開くと登録したデータをテーブルで一覧表示する。
データが登録されていない場合は「登録されていない」旨を表示する。
index.htmlに「新規登録」ボタンを設置し、登録画面に遷移する。
登録画面で10項目を入力するフォームを用意する。
登録画面で「登録」ボタンを押下し、データを登録する。成功時には一覧画面に遷移する。一覧には登録した全データが表示されていること
登録に失敗した場合は、エラー内容をalertで表示すること
一覧から任意のデータを選択し、そのデータの編集画面に遷移する。
編集画面で選択したデータの編集が行えること
編集画面に「更新」ボタンを押下し、データを更新する。成功時に一覧画面に遷移する。一覧には編集後のデータが表示されていること
失敗時にはエラー内容をalertで表示すること
一覧から任意のデータを選択し、「削除」ボタンを押下することでそのデータを削除する。
削除成功時には削除後の一覧が表示されること */

const FormComponent = () => {
    const [name, email, memo, gender,address, job, height] = useContext(FormContext)
    const [changeName, changeEmail, changeJob, changeAddress, changeMemo, changeHeight] = useContext(ChangeTextContext)
    const [putdata, checkedMen, checkedWomen] = useContext(CheckedContext)
    return (
        <form>    
            <input type="text" name={name} value={name} onChange={changeName} placeholder="名前"　/>
            <input type="text" name={email} value={email} onChange={changeEmail} placeholder="メールアドレス" />
            <label htmlFor="calendar">生年月日:</label>
            <input type="date" name="calendar" max="9999-12-31"></input>
            <input type="text" name={job} value={job} onChange={changeJob} placeholder="職業" />
            <input type="text" name={address} value={address} onChange={changeAddress} placeholder="住所" />
            <input type="radio" name={gender} onChange={checkedMen} /><label htmlFor="gender">男</label>
            <input type="radio" name={gender} onChange={checkedWomen}/><label htmlFor="gender">女</label>
            <label htmlFor={height}>身長</label><input type="number" name={height} value={height} onChange={changeHeight}/>
            <p>チェック<br />
                <input type="checkbox" name="check"/>チェック１
                <input type="checkbox" name="check"/>チェック2
                <input type="checkbox" name="check"/>チェック3
            </p>
            <select name="example" >
                <option>サンプル１</option>
                <option>サンプル２</option>
                <option>サンプル３</option>
            </select>
            <textarea name={memo} value={memo} onChange={changeMemo} placeholder="メモ"></textarea>
            {/*onclickでデータ登録 成功時に一覧画面遷移 失敗したらエラー内容をアラート*/}
            <button type="submit"  onClick={putdata}>登録</button>
        </form>
    )
}

type form = string[]
const FormContext = createContext<form>([])
const ChangeTextContext = createContext([(e:any) => {e.target.value}])
const CheckedContext = createContext([() => {}])
const App = () => {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [memo, setMemo] = useState('')
    const [gender, setGender] = useState('')
    const [job, setJob] = useState('')
    const [address, setAddress] = useState('')
    const [height, setHeight] = useState('')

    const changeName = (e:any) => {
        setName(e.target.value)
    }
    const changeEmail = (e:any) => {
        setEmail(e.target.value)
    }
    const changeJob = (e:any) => {
        setJob(e.target.value)
    }
    const changeAddress = (e:any) => {
        setAddress(e.target.value)
    }
    const changeMemo = (e:any)  => {
        setMemo(e.target.value)
    }
    const changeHeight = (e:any) => {
        setHeight(e.target.value)
    }
    const checkedMen = () => {
        setGender('男')
    }
    const checkedWomen = () => {
        setGender('女')
    }

    const req: VtecxApp.Entry[] = [
        {    
            user:{
                name: name,
                email: email,
                memo: memo,
                gender: gender,
                job: job,
                address: address,
                height:height
            },
            link: [
                {
                    "___href": "/foo/2",
                    "___rel": "self"
                }
            ]
        }
    ]
  
    const putdata = async () => {
        try {
          axios.defaults.headers['X-Requested-With'] = 'XMLHttpRequest'
          const res = await axios.put('/d/foo',req)
          console.log(res)
          console.log(req)
          console.log({name})
        } catch (e) {
          alert('error')
          console.log(e)
        }
      }

    
  return (
      <div>
          <FormContext.Provider value={[name, email, memo, gender,address, job, height]}>
              <ChangeTextContext.Provider value={[ changeName, changeEmail, changeJob, changeAddress, changeMemo, changeHeight]}>
                  <CheckedContext.Provider value={[putdata, checkedMen, checkedWomen]}>
                      <FormComponent />
                  </CheckedContext.Provider>
              </ChangeTextContext.Provider>
          </FormContext.Provider>
      </div>
  )

}

ReactDOM.render(<App/>, document.getElementById('container'))